html {
  overflow: hidden;
  overflow-y: scroll;
}
body {
  font-size: 16px;
  min-width: 320px;
  min-height: 100vh;
  position: relative;
  line-height: 1.65;
  font-family: worksans-medium, sans-serif;
  overflow-x: hidden;
  overflow-y: hidden;
  color: #000;
  -webkit-font-smoothing: antialiased;
}
body input,
body textarea {
  border: #666 1px solid;
  outline: 0;
}
body input:focus:required:invalid,
body textarea:focus:required:invalid {
  border-color: red;
}
body input:required:valid,
body textarea:required:valid {
  border-color: green;
}
.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}
.img-responsive.img-width {
  width: 100%;
}
.text-center {
  text-align: center;
}
b,
strong {
  font-weight: 700;
}
header {
  padding-top: 55px;
  padding-bottom: 55px;
  text-transform: uppercase;
  font-size: 20px;
}
header .logo {
  font-weight: 700;
  letter-spacing: 1px;
}
header .logo a {
  color: #000;
}
header .logo a:hover {
  color: #000;
  text-decoration: none;
}
header nav {
  width: 100%;
  text-align: right;
}
header nav ul.soc-icons {
  margin-left: 30px;
}
header nav ul.soc-icons li {
  padding-left: 0;
}
header nav ul.soc-icons li:not(:last-child) {
  padding-right: 15px;
}
header nav ul.soc-icons li img {
  /* height: 20px; */
  margin-top: -5px;
}
header nav ul.soc-icons li a:after {
  display: none;
}
header nav ul {
  display: inline-block;
  padding: 0;
  margin: 0;
}
header nav ul li {
  list-style-type: none;
  display: inline-block;
  padding-left: 20px;
}
header nav ul li:not(:last-child) {
  padding-right: 20px;
}
header nav ul li a {
  color: #000;
  position: relative;
  font-family: worksans-light, sans-serif;
  letter-spacing: 2px;
}
header nav ul li a:after {
  position: absolute;
  display: block;
  content: "";
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background-color: #000;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
header nav ul li a:hover {
  color: #000;
  text-decoration: none;
}
header nav ul li a:hover:after, .active_mnu:after {
  width: 100%;
}
.project-short-item {
  padding-top: 100%;
  display: block;
  width: 100%;
}
.project-short-item.project-short-big {
  padding-top: -webkit-calc(50% - 20px);
  padding-top: calc(50% - 20px);
}
.project-short-item:hover .uk-overlay {
  opacity: 1;
}
.project-short-item .head {
  color: #fff;
}
.project-short-item .uk-overlay {
  background-color: rgba(0, 0, 0, 0.6);
