  padding: 0;
}
.hamburger:active,
.hamburger:focus {
  outline: 0;
}
.uk-slidenav {
  color: #fff;
}
.uk-slidenav:hover {
  color: #fff;
}
.uk-slidenav svg {
  zoom: 1.5;
}
.product-content.content {
  margin-top: 0;
}
.content {
  margin-top: 25px;
  margin-bottom: 100px;
}
.content :not(.head) {
  font-family: worksans-light, sans-serif;
  font-size: 15px;
  letter-spacing: 1px;
}
.content a {
  color: #000;
  position: relative;
}
.content a:after {
  content: "";
  position: absolute;
  display: block;
  bottom: -3px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #000;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.content a:hover {
  color: #000;
  text-decoration: none;
}
.content a:hover:after {
  bottom: 0;
}
.content ul {
  padding: 0;
}
.content ul li {
  list-style-type: none;
  padding-left: 20px;
  position: relative;
}
.content ul li:after {
  content: "";
  display: block;
  position: absolute;
  width: 10px;
  height: 1px;
  background-color: #000;
  left: 0;
  top: 12px;
}
.news-item .content {
  max-width: 690px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 1200px) {
  .project-short-item.project-short-big {
    padding-top: -webkit-calc(50% - 15px);
    padding-top: calc(50% - 15px);
  }
}
@media (max-width: 940px) {
  header nav {
    position: fixed;
    height: 100vh;
    width: -webkit-calc(50% - 80px);
    width: calc(50% - 80px);
    top: 0;
    right: -50%;
    -webkit-transition: all 0.5s ease;
